'use client';

import { useAuth } from '@/context/AuthContext';
import Link from 'next/link';
import { usePathname } from 'next/navigation';

interface MenuItem {
  name: string;
  href: string;
  roles: string[];
}

const menuItems: MenuItem[] = [
  { name: 'Dashboard', href: '/dashboard', roles: ['admin', 'teacher', 'student', 'owner'] },
  { name: 'Student Management', href: '/dashboard/students', roles: ['admin', 'teacher', 'owner'] },
  { name: 'Teacher Management', href: '/dashboard/teachers', roles: ['admin', 'owner'] },
  { name: 'Leave Management', href: '/dashboard/leaves', roles: ['admin', 'teacher', 'owner'] },
  { name: 'Notice Management', href: '/dashboard/notices', roles: ['admin', 'teacher', 'owner'] },
  { name: 'Exam Results', href: '/dashboard/results', roles: ['admin', 'teacher', 'owner'] },
  { name: 'Notes Management', href: '/dashboard/notes', roles: ['admin', 'teacher', 'owner'] },
  { name: 'Bus Service', href: '/dashboard/bus', roles: ['admin', 'teacher', 'owner'] },
  { name: 'Syllabus Management', href: '/dashboard/syllabus', roles: ['admin', 'teacher', 'owner'] },
  { name: 'Time Table', href: '/dashboard/timetable', roles: ['admin', 'teacher', 'owner'] },
  { name: 'Attendance', href: '/dashboard/attendance', roles: ['admin', 'teacher', 'owner'] },
  { name: 'AI Search', href: '/dashboard/search', roles: ['student'] },
  { name: 'Password Reset', href: '/dashboard/password', roles: ['admin', 'teacher', 'student', 'owner'] },
];

export default function Sidebar() {
  const { user } = useAuth();
  const pathname = usePathname();

  if (!user) return null;

  const filteredMenuItems = menuItems.filter(item => 
    item.roles.includes(user.role)
  );

  return (
    <aside className="w-64 bg-gray-50 border-r border-gray-200 min-h-screen">
      <div className="p-6">
        <nav className="space-y-2">
          {filteredMenuItems.map((item) => {
            const isActive = pathname === item.href;
            return (
              <Link
                key={item.name}
                href={item.href}
                className={`block px-4 py-3 rounded-lg text-sm font-medium transition-colors ${
                  isActive
                    ? 'bg-blue-100 text-blue-700 border border-blue-200'
                    : 'text-gray-700 hover:bg-gray-100 hover:text-gray-900'
                }`}
              >
                {item.name}
              </Link>
            );
          })}
        </nav>
      </div>
    </aside>
  );
}
